<template>
  <div :class="classes">
    <label :style="labelStyle" class="label-label" v-if="boolean(label)">
      <span style="color:red;" v-if="required__">*</span>
      {{label}}
    </label>
    <div class="label-content me-flex">
      <slot>
        <div class="me-row me-cross-center label-content-slot"></div>
      </slot>
      <div class="label-content-error" v-show="validateStatus==='error'">{{validateMessage}}</div>
    </div>
  </div>
</template>
<script>
import { type, tools } from '@assets/script/common'
import Validator from 'async-validator'
export default {
  name: 'MeLabel',
  props: {
    rules: {
      type: [ Array, Object ],
      validator: value => type.isObjectOrArray(value)
    },
    prop: String,
    required: Boolean,
    label: String,
    flex: Boolean,
    labelStyle: { type: Object, default () { return {} } },
    readonly: Boolean
  },
  data () {
    return {
      required__: this.required,
      validateMessage: '',
      validateStatus: '',
      FormInstance: null,
      rulesForm: {},
      rules__: [],
      valueCurrent: '',
      valueDefault: null
    }
  },
  created () {
    this.setRules(this.rules)
  },
  computed: {
    classes () {
      return [
        'me-row me-label me-cross-start',
        { 'me-flex': this.flex }
      ]
    }
  },
  mounted () {
    if (this.prop) {
      this.dispatchUpward('MeForm', 'on-label-add', this)
      this.bindFormInstance()
    }
    this.bindFormElement()
  },
  beforeDestroy () {
    this.dispatchUpward('MeForm', 'on-label-remove', this)
  },
  methods: {
    bindEventToFormElement () {
      this.listener('on-label-blur', this.handlerElementBlur)
      this.listener('on-label-change', this.handlerElementChange)
    },
    handlerElementBlur () {
      this.validate()
    },
    handlerElementChange (value) {
      if (!this.valueDefault) {
        Reflect.set(this, 'valueDefault', value)
      }
      this.valueCurrent = value
      this.validate()
    },
    bindFormElement () {
      if (tools.isEmptyArray(this.$children)) { return }
      this.bindEventToFormElement()
    },
    bindFormInstance () {
      this.FormInstance = this.findParentComponent([ 'MeForm' ])
      this.bindFormRules(/*  prop */)
    },
    setRules (value) {
      if (tools.isEmpty(value)) { return }
      if (type.isArray(value) && value.length > 0) {
        this.rules__ = [ ...value ]
      }
      if (type.isObject(value)) {
        this.rules__ = [ value ]
      }
    },
    bindFormRules () {
      if (this.prop && this.FormInstance) {
        this.rulesForm = this.FormInstance.rules
        const ruleProp = Reflect.get(this.rulesForm, this.prop)
        if (this.$type.notArray(ruleProp)) {
          return
        }
        this.setRules(ruleProp)
      }
    },
    setValidateInfo (status, message) {
      this.validateStatus = status
      this.validateMessage = message
      this.$emit('on-label-status', status)
    },
    /**
     * 
     */
    validate () {
      const validator = new Validator({ [this.prop]: this.rules__ })
      const _this = this
      return new Promise((resolve, reject) => {
        validator.validate({ [this.prop]: this.valueCurrent }, { firstFields: true })
          .then(() => {
            _this.setValidateInfo('success')
            resolve('success')
          })
          .catch(({ errors }) => {
            const [ error ] = errors
            _this.setValidateInfo('error', error.message)
            reject(error.message)
          })
      })
    },
    reset () {
      console.log(`reset -> ${this.valueCurrent}, ${this.valueDefault}`)
      this.$emit('on-label-reset', this.valueDefault)
    }
  }
}
</script>
