// const TYPE_NULL = '[object Null]'
// const TYPE_UNDEFINED = '[object Undefined]'
const TYPE_NUMBER = '[object Number]'
const TYPE_BOOLEAN = '[object Boolean]'
const TYPE_OBJECT = '[object Object]'
const TYPE_ARRAY = '[object Array]'
const TYPE_FUNCTION = '[object Function]'
const TYPE_STRING = '[object String]'
const TYPE_REGEXP = '[object RegExp]'

class Type {
  /**
   * 
   * @param target 
   * @returns {string}
   */
  getType (target) {
    return toString.call(target)
  }

  /**
   * Booleantrue
   * @param target
   * @returns {Boolean}
   */
  isBoolean (target) {
    var type = this.getType(target)
    return type === TYPE_BOOLEAN
  }

  /**
   * Booleanfalse
   * @param target
   * @returns {Boolean}
   */
  notBoolean (target) {
    return this.isBoolean(target)
  }

  /**
   * Numbertrue
   * @param target 
   * @returns {Boolean}
   */
  isNumber (target) {
    var type = this.getType(target)
    return type === TYPE_NUMBER
  }

  /**
   * Numberfalse
   * @param target 
   * @returns {Boolean}
   */
  notNumber (target) {
    return !this.isNumber(target)
  }

  /**
   * Stringtrue
   * @param target 
   * @returns {Boolean}
   */
  isString (target) {
    var type = this.getType(target)
    return type === TYPE_STRING
  }

  /**
   * Stringfalse
   * @param target 
   * @returns {Boolean}
   */
  notString (target) {
    return !this.isString(target)
  }

  /**
   * Object Or Arraytrue
   * @param target 
   * @returns {Boolean}
   */
  isObjectOrArray (target) {
    var type = this.getType(target)
    return type === TYPE_OBJECT || type === TYPE_ARRAY
  }

  /**
   * Object Or Arrayfalse
   * @param target 
   * @returns {Boolean}
   */
  notObjectOrArray (target) {
    return !this.isObjectOrArray(target)
  }

  /**
   * Objecttrue
   * @param target 
   * @returns {Boolean}
   */
  isObject (target) {
    return this.getType(target) === TYPE_OBJECT
  }

  /**
   * Objectfalse
   * @param target 
   * @returns {Boolean}
   */
  notObject (target) {
    return !this.isObject(target)
  }

  /**
   * Arraytrue
   * @param target 
   * @returns {Boolean}
   */
  isArray (target) {
    return this.getType(target) === TYPE_ARRAY
  }

  /**
   * Arrayfalse
   * @param target 
   * @returns {Boolean}
   */
  notArray (target) {
    return !this.isArray(target)
  }

  isRegExp (target) {
    return this.getType(target) === TYPE_REGEXP
  }

  /**
   * Funcitontrue
   * @param target 
   * @returns {Boolean}
   */
  isFunction (target) {
    return this.getType(target) === TYPE_FUNCTION
  }

  /**
   * Funcitonfalse
   * @param target 
   * @returns {Boolean}
   */
  notFunction (target) {
    return !this.isFunction(target)
  }
}

export default new Type()
